// ** React Imports
import { Link } from 'react-router-dom'
import { Can } from '@src/utility/context/Can.js'
// ** Custom Components
import Avatar from '@components/avatar'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
// ** Store & Actions
import { store } from '@store/store'
import { getService, deleteService, updateStatus } from '../store'

// ** Icons Imports
import { MoreVertical, Trash2, Eye, Edit, CheckCircle, XCircle } from 'react-feather'

// ** Reactstrap Imports
import { Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Col } from 'reactstrap'
const MySwal = withReactContent(Swal)
import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()
// ** Renders Client Columns
const renderService = row => {
  return (<Avatar
    initials
    className='me-1'
    color='light-primary'
    content={row.name ? row.name.charAt(0).toUpperCase() : ''}
  />
  )
}

const deleteServicefun = (id) => {

  return MySwal.fire({
    title: 'Are you sure?',
    text: "You won't be able to revert this!",
    icon: 'warning',
    showCancelButton: true,
    confirmButtonText: 'Yes, delete it!',
    customClass: {
      confirmButton: 'btn btn-primary',
      cancelButton: 'btn btn-outline-danger ms-1'
    },
    buttonsStyling: false
  }).then(async (result) => {
    if (result.value) {
      await store.dispatch(deleteService(id))
      MySwal.fire({
        icon: 'success',
        title: 'Deleted!',
        text: 'Service has been deleted.',
        customClass: {
          confirmButton: 'btn btn-success'
        }
      })
    }
  })
}

const statusObj = [
  '',
  'light-primary',
  'light-warning'
]

const statusArr = [
  '',
  "Active",
  "Inactive"

]

export const columns = [
  {
    name: 'Service',
    sortable: true,
    minWidth: '300px',
    sortField: 'name',
    selector: row => row.name,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {renderService(row)}
        <div className='d-flex flex-column'>
          <Link to={`/services/view/${row.id}`}
            className='user_name text-truncate text-body'
            onClick={() => store.dispatch(getService(row.id))}
          >
            <span className='fw-bolder'>{row.name}</span>
          </Link>
        </div>
      </div>
    )
  },
  {
    name: 'Category',
    sortable: true,
    minWidth: '172px',
    sortField: 'role',
    selector: row => row.categoriesname,
    cell: row => <Link to={`/services/view/${row.id}`}><span className='text-capitalize text-truncate text-body'>{row.categoriesname}</span></Link>
  },
  {
    name: 'Tax Rate',
    minWidth: '138px',
    maxWidth: '150px',
    sortable: true,
    sortField: 'status',
    selector: row => row.status,
    cell: row => (<Link to={`/services/view/${row.id}`}>
      <span>
        {row.taxrate}</span>
    </Link>
    )
  },
  {
    name: 'Professional Fee',
    minWidth: '138px',
    maxWidth: '200px',
    center: true,
    sortable: true,
    sortField: 'status',
    selector: row => row.status,
    cell: row => (<Link to={`/services/view/${row.id}`}>
      <span>{row.professionalfee}</span>
    </Link>
    )
  },
  {
    name: 'Status',
    minWidth: '138px',
    maxWidth: '150px',
    sortable: true,
    sortField: 'status',
    selector: row => row.status,
    cell: row => (
      <Badge className='text-capitalize' color={statusObj[row.status]} pill>
        {statusArr[row.status]}
      </Badge>
    )
  },
  {
    name: 'Actions',
    minWidth: '100px',
    maxWidth: '150px',
    cell: row => (
      <div className='column-action d-flex align-items-center'>
        <Can I={'read'} a={'service'}>
          <Col tag={Link} lg={4}
            to={`/services/view/${row.id}`}
            onClick={() => store.dispatch(getService(row.id))}>
            <Eye className='cursor-pointer mt-0' size={16} />
          </Col>
        </Can>
        <Can I={'write'} a={'service'}>
          <Col lg={4} tag={Link} to={`/services/edit/${row.id}`}
            onClick={() => store.dispatch(getService(row.id))}>
            <Edit
              className='cursor-pointer ms-1 mt-0' size={16} />
          </Col>
        </Can>

        <UncontrolledDropdown>
          <DropdownToggle tag='div' className='btn btn-sm'>
            <MoreVertical size={14} className='cursor-pointer' />
          </DropdownToggle>

          <DropdownMenu>
            {row.status === 2 && (
              <Can I={'write'} a={'service'}>
                <DropdownItem tag='a' href='/' className='w-100' onClick={e => {
                  e.preventDefault()
                  const id = row.id
                  const status = 1
                  const obj = { id, status, updatedBy: userId }
                  store.dispatch(updateStatus(obj))
                }}>
                  <CheckCircle size={14} className='me-50' />
                  <span className='align-middle'>Mark as Active</span>
                </DropdownItem>
              </Can>)
            }
            {row.status === 1 && (
              <Can I={'write'} a={'service'}>
                <DropdownItem tag='a' href='/' className='w-100' onClick={e => {
                  e.preventDefault()
                  const id = row.id
                  const status = 2
                  const obj = { id, status, updatedBy: userId }
                  store.dispatch(updateStatus(obj))
                }}>
                  <XCircle size={14} className='me-50' />
                  <span className='align-middle'>Mark as Inactive</span>
                </DropdownItem>
              </Can>)}
            <Can I={'delete'} a={'service'}>
              <DropdownItem
                tag='a'
                href='/'
                className='w-100'
                onClick={e => {
                  e.preventDefault()
                  deleteServicefun(row.id)
                }}
              >
                <Trash2 size={14} className='me-50' />
                <span className='align-middle'>Delete</span>
              </DropdownItem>
            </Can>
          </DropdownMenu>
        </UncontrolledDropdown>
      </div>
    )
  }
]
